const initialState = {
    isCreated: false
}

const inputReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'REMINDER_CREATED_SUCCESS':
            return {
                ...state,
                isCreated: true
            }
        case 'NOT_CREATED':
            return {
                ...state,
                isCreated: false
            }
        default:
            return state
    }
}
export default inputReducer;