import React, { useState, useEffect } from 'react'

// Redux
import { connect } from 'react-redux'
import { logoutFirebase, createReminder } from '../../Store/ActionCreators';

// User
import './InputForm.css'
import LogoutIcon from '../../Assets/Icons/logout.svg';

// Material UI Imports
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';

// Third Party
import { ToastsContainer, ToastsStore } from 'react-toasts';

//----------------------------------------------------------------------------------------------------



const inputForm = (props) => {

    const initialState = {
        value: '',
        valid: false
    }


    const [selectedDate, setSelectedDate] = useState(cleanDate(new Date()));
    const [reminderFor, setreminderFor] = useState(initialState)
    const [associateName, setassociateName] = useState(initialState)
    const [submitClicked, setsubmitClicked] = useState(false)
    const [chk1, setchk1] = useState(initialState)
    const [chk2, setchk2] = useState(initialState)
    const [chk3, setchk3] = useState({
        value: '0',
        valid: true
    })

    // Hooks Section Starts -------------------------------------------------------------------

    useEffect(() => {
        if (props.isCreated) {
            ToastsStore.success("Reminder Created")
        }
        return () => {
            props.isNotCreated()
        }
    }, [props.isCreated])

    // Hooks Section Ends ---------------------------------------------------------------------

    // Helper Function Section Starts -------------------------------------------------------------------
    function cleanDate(date) {
        let dateArr = date.toDateString().split(' ')
        dateArr.shift()
        return dateArr.join('-')
    }

    function capitalize(str) {
        let firstLetter = str.charAt(0).toUpperCase();
        let remainingLetters = str.slice(1);
        return firstLetter + remainingLetters;
    }

    function reminderFiller() {
        let reminders = []
        if (chk1.valid) reminderPusher(reminders, chk1)
        if (chk2.valid) reminderPusher(reminders, chk2)
        if (chk3.valid) reminderPusher(reminders, chk3)
        reminders.sort()
        return reminders
    }

    function reminderPusher(arr, check) {
        return arr.push({
            days: parseInt(check.value),
            isReminded: false
        })
    }

    function checkValidThenSet(e, func) {
        setsubmitClicked(false)
        const val = e.target.value;
        const isValid = val !== '';
        func({ value: capitalize(val), valid: isValid })
    }

    const isAllInputValid = () => reminderFor.valid && associateName.valid && (chk1.valid || chk2.valid || chk3.valid) && selectedDate !== 'Date';

    // Helper Function Section Ends ---------------------------------------------------------------------

    // Handler Section Starts -------------------------------------------------------------------
    const handleDateChange = date => setSelectedDate(cleanDate(date));
    const checkHandler = (e, check, func) => {
        const newValid = !check.valid
        func({
            value: e.target.value,
            valid: newValid
        })
    }

    const clearButtonHandler = () => {
        setsubmitClicked(false)
        setreminderFor(initialState)
        setassociateName(initialState)
        setchk1(initialState)
        setchk2(initialState)
        setSelectedDate(cleanDate(new Date()))
    }

    const submitHandler = (e) => {
        e.preventDefault();
        setsubmitClicked(true);
        console.log(selectedDate)
        console.log(typeof selectedDate)
        let data = {
            reminderFor: reminderFor.value,
            associateName: associateName.value,
            eventDate: selectedDate,
            reminders: reminderFiller()
        }
        props.createReminderHandler(data)
        clearButtonHandler()
    }
    // Handler Section Ends ---------------------------------------------------------------------


    return (
        <div className="inputForm-main-div">
            <ToastsContainer store={ToastsStore} />
            <div className="title-div"><h2 className="title-login">Reminder</h2><button onClick={props.logoutClicked} className="logout-button" style={{ backgroundImage: `url(${LogoutIcon})` }}></button></div>
            <form className="input-form">
                <TextField
                    error={!reminderFor.valid && submitClicked}
                    value={reminderFor.value}
                    onChange={(e) => checkValidThenSet(e, setreminderFor)}
                    name="reminderFor"
                    id="outlined-basic"
                    label="Reminder For"
                    variant="outlined" />

                <TextField

                    error={!associateName.valid && submitClicked}
                    onChange={(e) => checkValidThenSet(e, setassociateName)}
                    value={associateName.value}
                    name="associatedName"
                    id="outlined-basic"
                    label="Associated Name "
                    variant="outlined" />

                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <KeyboardDatePicker

                        margin="normal"
                        id="date-picker-dialog"
                        label="Event Date"
                        format="MM-dd-yyyy"
                        value={selectedDate}
                        onChange={handleDateChange}
                        KeyboardButtonProps={{
                            'aria-label': 'change date',
                        }}
                    />
                </MuiPickersUtilsProvider>

                <div className="checkbox-div">
                    <h4 className="checkbox-title">When do you want us to remind you?</h4>
                    <FormControlLabel
                        onChange={(e) => checkHandler(e, chk1, setchk1)}
                        checked={chk1.valid}
                        value="7"
                        control={<Checkbox color="primary" />}
                        label="7 Days Before"
                        labelPlacement="left"
                    />
                    <FormControlLabel
                        onChange={(e) => checkHandler(e, chk2, setchk2)}
                        checked={chk2.valid}
                        value="3"
                        control={<Checkbox color="primary" />}
                        label="3 Days Before"
                        labelPlacement="left"
                    />

                    <FormControlLabel
                        onChange={(e) => checkHandler(e, chk3, setchk3)}
                        checked={chk3.valid}
                        value="0"
                        control={<Checkbox color="primary" />}
                        label="On the event day"
                        labelPlacement="left"

                    />

                </div>
                <div className="button-div">
                    <Button disabled={!isAllInputValid()} onClick={submitHandler} style={{ marginRight: '10px' }} variant="contained" color="primary">Create Reminder</Button>
                    <Button onClick={clearButtonHandler} variant="contained" color="secondary">Clear</Button>
                </div>

            </form>




        </div>
    )
}
const mapStateToProps = (state) => {
    return {
        isCreated: state.inputReducer.isCreated
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        logoutClicked: () => dispatch(logoutFirebase()),
        createReminderHandler: (data) => dispatch(createReminder(data)),
        isNotCreated: () => dispatch({ type: 'NOT_CREATED' })


    }
}
export default connect(mapStateToProps, mapDispatchToProps)(inputForm);
